body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0px;
  background-color: lightpink;
  font-family: 'Roboto', sans-serif;
}
.panel {
  background-color: lightgrey;
  padding: 20px;
  border-radius: 30px;
  width: 100%;
  max-width: 400px;
  margin: 10px;
}
.search {
  display: flex;
  align-items: center;
  justify-content: center;
}
button {
  margin: 10px;
  border-radius: 50%;
  border: none;
  height: 30px;
  width: 30px;
}
.searchBar {
  border: none;
  outline: none;
  padding: 10px;
  border-radius: 15px;
  font-family: 'Roboto', sans-serif;
  font-size: 100%;
}
button:hover {
  background-color: aquamarine;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.temperature {
  margin: 0px;
}